    function setFsFavicon(id) { // 部隊編成：お気に入り設定
      const nikke = _this.nikkes.find( d => { return d.id === id.replace("markFavi",""); })
      if (nikke.favicon == 0) {
        nikke.favicon = 1;
      } else {
        nikke.favicon = 0;
      };
      _this.$set(_this.nikkes, 0, _this.nikkes[0]); // 配列でも画面へ即時反映されるようsetする

      // クッキーへお気に入りを保存
      const nikkeNo = nikke.originId;
      let before = _this.fsFaviconList.slice(0, nikkeNo-1);
      let after = _this.fsFaviconList.slice(nikkeNo);
      _this.fsFaviconList = before + nikke.favicon + after;
      localStorage.setItem('sfFaviconList', _this.fsFaviconList);
