    async function wait(millisec) {
      return new Promise(resolve => setTimeout(resolve, millisec));
    };

    // イベント設定など
    if ('ontouchstart' in window || navigator.maxTouchPoints) {
      _this.coTouchDevice = true; //タッチデバイスフラグON
      document.addEventListener("touchstart", (ev) => {
        // ツールチップが存在する場合はそれを削除して終了
        if (removeToolTip()) return;

        if (ev.target.tagName === "DIV") {
          if (!ev.target.className.includes('tooltipX')) {
            if (ev.target.id.startsWith('sfnikke')) {
              tooltipNikkeId = ev.target.id;
              tooltipStart(ev.target, true);
            };
            if (ev.target.parentNode.id.startsWith('sfnikke')) {
              tooltipNikkeId = ev.target.parentNode.id;
              tooltipStart(ev.target.parentNode, true);
            };
          };
        };

        if (ev.target.tagName === "DIV") {
          if (ev.target.id.startsWith('sfnikke')) {
            mdown(ev, ev.target);
          } else if (ev.target.id.startsWith('smnikke')) {
            mdown(ev, ev.target);
          };
        };
      });
      document.addEventListener("touchmove", (ev) => {
        if (document.getElementsByClassName("dragTag")[0] != undefined) {
          mmove(ev);
        };
      });
      //document.addEventListener('touchmove', noscroll, {passive: false})
      document.addEventListener('touchend', (ev) => {
        if (document.getElementsByClassName("dragTag")[0] != undefined) {
          mup(ev);
        };
      });
      //document.addEventListener('touchleave', removeToolTip);
      //document.addEventListener('scroll', noscroll, {passive: false});
      //document.addEventListener('wheel', noscroll, {passive: false});
    } else {
      document.addEventListener("mouseover", (ev) => {
        // ツールチップイベント設定
        if (ev.target.tagName === "DIV") {
          if (!ev.target.className.includes('tooltipX')) {
            if (ev.target.id.startsWith('sfnikke')) {
              if (document.getElementsByClassName("dragTag")[0] === undefined) {
                tooltipNikkeId = ev.target.id;
                tooltipStart(ev.target, false);
              };
            } else if (ev.target.parentNode.id.startsWith('sfnikke')) {
              if (document.getElementsByClassName("dragTag")[0] === undefined) {
                tooltipNikkeId = ev.target.parentNode.id;
                tooltipStart(ev.target.parentNode, false);
              };
            };
          };
        };
      });
      document.addEventListener("mousedown", (ev) => {
        removeToolTip(); // ツールチップ削除
        // ニケ画像操作
        if (ev.target.tagName === "DIV") {
          if (ev.target.id.startsWith('sfnikke')) {
            mdown(ev, ev.target);
          } else if (ev.target.id.startsWith('smnikke')) {
            mdown(ev, ev.target);
          } else if (ev.target.id.startsWith('markBurst')) {
            mdown(ev, ev.target.parentNode);
          } else if (ev.target.id.startsWith('markElemental')) {
            mdown(ev, ev.target.parentNode);
          } else if (ev.target.id.startsWith('sfSortKey')) {
            mdown(ev, ev.target);
          };
        };
      });
      //document.addEventListener('mousemove', removeToolTip);
      document.addEventListener("mousemove", (ev) => {
        if (document.getElementsByClassName("dragTag")[0] != undefined) {
          mmove(ev);
        };
      });
      //document.addEventListener("mousemove", (ev) => {
      //  if (document.getElementsByClassName("dragTag")[0] != undefined) {
      //    mmove(ev);
      //  };
