      //document.addEventListener('mousemove', noscroll, {passive: false})
      document.addEventListener("mouseup", (ev) => {
        if (document.getElementsByClassName("dragTag")[0] != undefined) {
          mup(ev);
        };
      });
      document.addEventListener('mouseout', removeToolTip);
      document.addEventListener('mouseleave', removeToolTip);
      document.addEventListener('scroll', removeToolTip);
    };
