        targetParentId = ev.target.parentNode.id;
        if (ev.target.parentNode.parentNode != undefined) {
          targetGrandParentId = ev.target.parentNode.parentNode.id;
        };
      }
      if (targetParentId == null) targetParentId = "";
      if (targetGrandParentId == null) targetGrandParentId = "";

      // 部隊編成：部隊セットのアコーディオンが開いていたら閉じる
      if (!targetId.includes("sfAccordionTable")
          && !targetParentId.includes("sfAccordionTable")
          && !targetGrandParentId.includes("sfAccordionTable")) {
        for (let i=1; i<=5; ++i) {
          let summamry = document.getElementById("sfSquadSetSummary" + i);
          if (summamry != undefined) {
            summamry.parentElement.open = "";
