    const asyncFunc = async () => { // 外部データ読み込み
      //ニケ一覧を加工
      let nikkeOriginId = 0;
      _this.nikkes.forEach(function(item){
        if (item.skill1Treasure == "1")      item.skill1 += "　<b><font color=\"green\">宝もの第" + item.skill1Treasure + "段階強化</font></b>";
        else if (item.skill1Treasure == "2") item.skill1 += "　<b><font color=\"blue\">宝もの第" + item.skill1Treasure + "段階強化</font></b>";
        else if (item.skill1Treasure == "3") item.skill1 += "　<b><font color=\"red\">宝もの第" + item.skill1Treasure + "段階強化</font></b>";
        item.skill1Infos.forEach(function(info){
          if (info.note.startsWith("・")) {
            item.skill1 += "<br><font color=\"steelblue\">" + info.note + "</font>";
          } else {
            item.skill1 += "<br>" + info.note;
          };
        });

        if (item.skill2Treasure == "1")      item.skill2 += "　<b><font color=\"green\">宝もの第" + item.skill2Treasure + "段階強化</font></b>";
        else if (item.skill2Treasure == "2") item.skill2 += "　<b><font color=\"blue\">宝もの第" + item.skill2Treasure + "段階強化</font></b>";
        else if (item.skill2Treasure == "3") item.skill2 += "　<b><font color=\"red\">宝もの第" + item.skill2Treasure + "段階強化</font></b>";
        item.skill2Infos.forEach(function(info){
          if (info.note.startsWith("・")) {
            item.skill2 += "<br><font color=\"steelblue\">" + info.note + "</font>";
          } else {
            item.skill2 += "<br>" + info.note;
          };
        });

        if (item.burstTreasure == "1")      item.burst += "　<b><font color=\"green\">宝もの第" + item.burstTreasure + "段階強化</font></b>";
        else if (item.burstTreasure == "2") item.burst += "　<b><font color=\"blue\">宝もの第" + item.burstTreasure + "段階強化</font></b>";
        else if (item.burstTreasure == "3") item.burst += "　<b><font color=\"red\">宝もの第" + item.burstTreasure + "段階強化</font></b>";
        item.burstInfos.forEach(function(info){
          if (info.note.startsWith("・")) {
            item.burst += "<br><font color=\"steelblue\">" + info.note + "</font>";
          } else {
            item.burst += "<br>" + info.note;
          };
        });

        item.originId = ++nikkeOriginId; // 初期ID
        item.favicon = 0; // お気に入り初期値
      });

      // クッキーからお気に入りを読み込み
      _this.fsFaviconList = localStorage.getItem('sfFaviconList', 1);
      if (_this.fsFaviconList == null) _this.fsFaviconList = "";
      let faviconIdx = 0;
      _this.nikkes.forEach(function(nikke){
        if (faviconIdx < _this.fsFaviconList.length) {
          nikke.favicon = _this.fsFaviconList.charAt(faviconIdx);
        } else {
          _this.fsFaviconList += "0";
        };
        ++faviconIdx;
      });

      // 面談データを加工
      _this.talks.forEach(function(nikke){
        let margeSelect = "";
        let margeHiragana = "";
        nikke.cases.forEach(function(caseItem){ // 選択肢1～2
          //caseItem.trigramify = _this.trigramify(caseItem.select); // マッチング用データ
          margeSelect += caseItem.select;
          margeHiragana += caseItem.hiragana;
        });
        nikke.select = margeSelect;                       // 選択肢(マージ)
        nikke.hiragana = margeHiragana;                   // 選択肢平仮名(マージ)
        nikke.trigramify = _this.trigramify(margeSelect); // マッチング用データ
        nikke.hit = 0;
      });

      // 名前順でソートしたニケ一覧を作成
      _this.nikkes_sort_name = _this.nikkes.concat();
      _this.nikkes_sort_name.sort((a,b) => {
        if (a.name > b.name) { return 1 }
        if (a.name < b.name) { return -1 }
      });

      // クッキーからニケ並び順の設定を読み込み
      let idx = 0;
      _this.coNikkeSortKey.forEach(function(nikkeSort){
        if(localStorage.getItem('coNikkeSortKey['+idx+']', 1) != null){
          nikkeSort.SortNum = localStorage.getItem('coNikkeSortKey['+idx+']', 1);
        };
        ++idx;
      });
      _this.coNikkeSortKey.sort((a,b) => {
        if (a.SortNum > b.SortNum) { return 1 }
        if (a.SortNum < b.SortNum) { return -1 }
      });

      // ニケ一覧を並び替え
      _this.nikkes.forEach(function(nikke){
        nikke.allSort = "";
        _this.coNikkeSortKey.forEach(function(nikkeSort){
          if(nikkeSort.Name == "名前") {
            nikke.allSort += String(nikke.name).padEnd(20, "＿");
          } else if(nikkeSort.Name == "等級") {
            nikke.allSort += String(nikke.rearitySort).padStart(3, "0");
          } else if(nikkeSort.Name == "バースト") {
            nikke.allSort += String(nikke.burstclassSort).padStart(3, "0");
          } else if(nikkeSort.Name == "属性") {
            nikke.allSort += String(nikke.elementalSort).padStart(3, "0");
          } else if(nikkeSort.Name == "武器") {
            nikke.allSort += String(nikke.weaponSort).padStart(3, "0");
          } else if(nikkeSort.Name == "クラス") {
            nikke.allSort += String(nikke.classSort).padStart(3, "0");
          } else if(nikkeSort.Name == "企業") {
            nikke.allSort += String(nikke.manufacturerSort).padStart(3, "0");
          } else if(nikkeSort.Name == "実装順") {
            nikke.allSort += String(10000 - nikke.implementation).padStart(3, "0");
          };
        });
      });
      _this.nikkes.sort((a,b) => {
        if (a.allSort > b.allSort) { return 1 }
        if (a.allSort < b.allSort) { return -1 }
      });
      let idCnt = 1;
      _this.nikkes.forEach(function(nikke){
        nikke.id = "nikke" + String(idCnt).padStart(3, "0");
        ++idCnt;
      });

      // 面談候補ニケのリストを作成～並び替え
      _this.talks.forEach(function(nikke){
        if (!_this.tsTalkNikkes.some(item => item.name === nikke.name)) {
          let newNikke = JSON.parse(JSON.stringify(_this.tsTalkNikkeProt));
          newNikke.id = nikke.id;
          newNikke.name = nikke.name;
          newNikke.allSort = _this.nikkes.find(e => e.name === nikke.name).allSort;
          newNikke.burstclass = nikke.burstclass;
          newNikke.check = false;
          _this.tsTalkNikkes.push(newNikke);
        };
      });
      _this.tsTalkNikkes.sort((a,b) => {
        if (a.allSort > b.allSort) { return 1 }
        if (a.allSort < b.allSort) { return -1 }
      });
    };

