  methods: {
    mCoSetDark(flgChange) { // 共通：ダークモード設定
      let _this = this;
  
      if (flgChange) {
        if (_this.isDark == 0) {
          _this.isDark = 1;
        } else {
          _this.isDark = 0;
        };
      };

      if (_this.isDark == 1) {
        document.documentElement.setAttribute("class", "dark");
      } else {
        document.documentElement.setAttribute("class", "");
      };
      localStorage.setItem('isDark', _this.isDark);
    },

    mMcCalcMaintain() { // お手入れ確率計算：計算実行
      let _this = this;

      //let time100 = 0;
      //let time200 = 0;
      //let time300 = 0;
      //let time350 = 0;
      //let time351 = 0;
      //let time352 = 0;
      //let time353 = 0;
      //let time354 = 0;
      //let time355 = 0;
      //let time360 = 0;
      //let time400 = 0;
      //let time100sum = 0;
      //let time200sum = 0;
      //let time300sum = 0;
      //let time350sum = 0;
      //let time351sum = 0;
      //let time352sum = 0;
      //let time353sum = 0;
      //let time354sum = 0;
      //let time355sum = 0;
      //let time360sum = 0;
      //let time400sum = 0;

      //ブラウザが再描画可能なタイミングでコールバック関数を実行 
      //const callback = () => {
      //  requestAnimationFrame(callback); 
      //}
      //callback();

      // 入力値是正
      for(let i = 0; i <= 2; i++) {
        if(_this.mcUseKit[i] < 0) _this.mcUseKit[i] = 0; // 0以下なら0にする
        if(!Number.isInteger(_this.mcUseKit[i])) _this.mcUseKit[i] = Math.floor(_this.mcUseKit[i]); // 整数ではない場合は小数点以下切り捨て
      };

      // 入力値が0なら初級キットを1にする
      if (_this.mcUseKit[0] == 0 && _this.mcUseKit[1] == 0 && _this.mcUseKit[2] == 0) {
        _this.mcUseKit[0] = 1;
      };

      if (_this.mcUseKit[0] + _this.mcUseKit[1] + _this.mcUseKit[2] > 8) {
        alert("処理に時間が掛かりすぎる事が予想されるため計算を中止します。\nキット使用数は3種合計で8以下にしてください。");
        _this.mcUseKit[0] = _this.mcUseKitPre[0];
        _this.mcUseKit[1] = _this.mcUseKitPre[1];
        _this.mcUseKit[2] = _this.mcUseKitPre[2];

      } else {
        _this.mcUseKitPre[0] = _this.mcUseKit[0];
        _this.mcUseKitPre[1] = _this.mcUseKit[1];
        _this.mcUseKitPre[2] = _this.mcUseKit[2];

        // 計算結果一覧をクリア
        _this.mcResultList.splice(0);

        // お手入れキット使用パターンを計算結果一覧にセット
        //time100 = performance.now();
        _this.mMcCombinationDoll(
                            _this.mcUseKit[0]
                          , _this.mcUseKit[1]
                          , _this.mcUseKit[2]
                          , ""
                          , 0
                          , _this.mcUseKit[0] + _this.mcUseKit[1] + _this.mcUseKit[2]);
        //time100sum += performance.now() - time100;

        // 成功・大成功パターンを計算結果一覧にセット
        //time200 = performance.now();
        //for (let i=0; i<=2 ** (_this.mcUseKit[0] + _this.mcUseKit[1] + _this.mcUseKit[2]) - 1; ++i) {
        //  let newDetail = JSON.parse(JSON.stringify(_this.mcDetail));
        //  newDetail.experience = parseInt(_this.mcDollExperience); // 初期経験値
        //  _this.mcResultList[0].detailList.push(newDetail);
        //};
        //for (let i=1; i<=_this.mcResultList.length-1; ++i) {
        //  let newDetailList = JSON.parse(JSON.stringify(_this.mcResultList[0].detailList));
        //  _this.mcResultList[i].detailList = newDetailList;
        //};
        _this.mMcCombinationSuccess(
                            ""
                          , 0
                          , _this.mcUseKit[0] + _this.mcUseKit[1] + _this.mcUseKit[2]);
        //time200sum += performance.now() - time200;
        
        // 期待値計算
        let maximumExpected = 0;
        let maximumAverage = 0;
        let maximumMedian = 0;
        let maximumProbability = 0;
        //time300 = performance.now();
        _this.mcResultList.forEach(function(result){
          let num = 0;
          let medianList = [];
          let containAll = "/";
          //time350 = performance.now();
          result.detailList.forEach(function(detail){
            let used = [0, 0, 0];
            let containWord = "";
            //time351 = performance.now();
            for (let idx = 0; idx <= detail.patternList.length - 1; ++idx) {
              // 経験値が上限に達していない場合のみ処理
              if (detail.experience < _this.MC_EXPERIENCE_DOLL[_this.mcDollId].exp*15) {
                // この回の確率
                let probability = _this.mMcGetProbability(detail.patternList[idx], detail.successList[idx], detail.experience);

                // パターンを通しての確率
                detail.probability *= probability;

                // パターンのお手入れ後経験値
                detail.experience = _this.mMcGetExperience(detail.patternList[idx], detail.successList[idx], detail.experience);

                // 表示
                if (detail.note != ""){
                  detail.note += "→";
                };
                detail.note += _this.mMcGetKitName(detail.patternList[idx])
                              + _this.mMcGetSuccessName(detail.successList[idx])
                              + "(" + (probability*100).toPrecision(3) + "%)";
                
                containWord += detail.patternList[idx] + "" + detail.successList[idx];

                // 確率が0%の場合このパターンは強制終了
                if (probability == 0) {
                  break;
                };

                // 使用したキットをレア度ごとにカウント
                used[detail.patternList[idx]] += 1;
              };
            };
            //time351sum += performance.now() - time351;

            // 獲得経験値×確率
            //time352 = performance.now();
            detail.expected = (detail.experience - _this.mcDollExperience) * detail.probability;
            //time352sum += performance.now() - time352;
            
            // 表示
            //time353 = performance.now();
            detail.note += "<br>獲得経験値：" + (detail.experience - _this.mcDollExperience).toLocaleString()
                          + "<br>発生確率：" + (detail.probability*100).toPrecision(3) + "%"
                          + "<br>期待値(獲得経験値×発生確率)：" + Math.round(detail.expected).toLocaleString();
            if (_this.mcUseKit[0]-used[0] > 0 || _this.mcUseKit[1]-used[1] > 0 || _this.mcUseKit[2]-used[2] > 0) {
              let buf = "";
              if (_this.mcUseKit[0]-used[0] > 0) buf += "R" + (_this.mcUseKit[0] - used[0]) + "個";
              if (_this.mcUseKit[1]-used[1] > 0) {
                if (buf != "") buf += "／";
                buf += "SR" + (_this.mcUseKit[1] - used[1]) + "個";
              };
              if (_this.mcUseKit[2]-used[2] > 0) {
                if (buf != "") buf += "／";
                buf += "SSR" + (_this.mcUseKit[2] - used[2]) + "個";
              };
              detail.note += "<br>キット余り：" + buf;
            } else {
              detail.note += "<br>キット余り：なし";
            }
            detail.note += "<br>";
            //time353sum += performance.now() - time353;

            // 同一の詳細があるかチェック（お手入れ途中で経験値が上限に達した場合はあり得る）
            //time354 = performance.now();
            let flgFind = false;
            flgFind = containAll.includes(containWord);
            containAll += containWord + "/";
            //result.detailList.forEach(function(detailCheck){
            //  if (detail.note == detailCheck.note) {
            //    if (detail.id != detailCheck.id) {
            //      flgFind = true;
            //    };
            //  };
            //});
            
            //flgFind = result.detailList.find(data=> {
            //  return (data.id != detail.id && data.note == detail.note);
            //});
            
            //for (let idx = 0; idx <= result.detailList.length - 1; ++idx) {
            //  if (result.detailList[idx].id >= detail.id) {
            //    break;
            //  } else if (result.detailList[idx].note == detail.note) {
            //    flgFind = true;
            //    break;
            //  };
            //};
            //time354sum += performance.now() - time354;

            //time355 = performance.now();
            if (flgFind || detail.probability == 0) {
              detail.flgValid = false; // 同一の詳細がある、または、発生確率0%の場合は無効化
            } else {
              detail.num = ++num;
              result.expectedSum += detail.expected; // 期待値へ加算
              result.average += detail.experience - _this.mcDollExperience; // 平均値計算の準備
              medianList.push(detail.experience - _this.mcDollExperience); // 中央値計算の準備
              
              // 15段階到達の場合は15段階到達確率に加算
              if (detail.experience >= _this.MC_EXPERIENCE_DOLL[_this.mcDollId].exp*15) {
                result.probabilityMaximum += detail.probability;
              };

              // キットの余りの累計
              for (let i=0; i<=2; ++i) {
                let amari = _this.mcUseKit[i]-used[i];
                if (amari >= 1 && amari <= 10) {
                  detail.remainder[i][amari] += detail.probability;
                };
              };
            };
            //time355sum += performance.now() - time355;
          });
          //time350sum += performance.now() - time350;

          //time360 = performance.now();

          // 平均値計算
          result.average /= num;

          // 中央値計算
          function compareFunc(a, b) {
            return a - b;
          }
          medianList.sort(compareFunc);
          if (medianList.length > 0) {
            if (medianList.length % 2 == 1) {
              result.median = medianList[Math.floor(medianList.length/2)];
            } else {
              result.median = (
                                medianList[Math.floor(medianList.length/2)-1]
                              + medianList[Math.floor(medianList.length/2)]
                              ) / 2;
            };
          };

          // 結果一覧内の最大値を更新
          if (maximumExpected < result.expectedSum) maximumExpected = result.expectedSum;
          if (maximumAverage < result.average) maximumAverage = result.average;
          if (maximumMedian < result.median) maximumMedian = result.median;
          if (maximumProbability < result.probabilityMaximum) maximumProbability = result.probabilityMaximum;

          // キットの余りの累計
          result.detailList.forEach(function(detail){
            for (let i=0; i<=2; ++i) {
              let probability = 0;
              for (let j=10; j>=0; --j) {
                if (detail.remainder[i][j] > 0) {
                  result.remainderSum[i][j] += detail.remainder[i][j] + probability;
                  probability += result.remainderSum[i][j];
                };
              };
            };
          });
          
          //time360sum += performance.now() - time360;
        });
        //time300sum += performance.now() - time300;

        // 結果の表示項目へ追記
        //time400 = performance.now();
        _this.mcResultList.forEach(function(result){
          // タイトル
          result.title += '　';
          if (_this.mcDisp[0] == 1) {
            result.title += "期待値：" + Math.round(result.expectedSum, 1).toLocaleString();
            if (maximumExpected.toFixed(5) == result.expectedSum.toFixed(5)) {
              result.maximumFlag = true;
              result.title += '★　';
            } else {
              result.title += '　　';
            };
          };
          if (_this.mcDisp[1] == 1) {
            if (_this.mcDisp[1] == 1) result.title += "平均値：" + Math.round(result.average, 1).toLocaleString();
            if (maximumAverage.toFixed(5) == result.average.toFixed(5)) {
              result.maximumFlag = true;
              result.title += '★　';
            } else {
              result.title += '　　';
            };
          };
          if (_this.mcDisp[2] == 1) {
            if (_this.mcDisp[2] == 1) result.title += "中央値：" + Math.round(result.median, 1).toLocaleString();
            if (_this.mcDisp[2] == 1 && maximumMedian.toFixed(5) == result.median.toFixed(5)) {
              result.maximumFlag = true;
                result.title += '★　';
            } else {
              result.title += '　　';
            };
          };
          if (_this.mcDisp[3] == 1) {
            if (_this.mcDisp[3] == 1) result.title += "15段階到達確率：" + (result.probabilityMaximum*100).toPrecision(3) + "%";
            if (_this.mcDisp[3] == 1 && maximumProbability.toFixed(5) == result.probabilityMaximum.toFixed(5)) {
              result.maximumFlag = true;
              result.title += '★　';
            } else {
              result.title += '　　';
            };
          };

          // 内容
          let amariStr = ["", "", ""];
          for (let i=0; i<=2; ++i) {
            for (let j=10; j>=0; --j) {
              if (result.remainderSum[i][j] > 0) {
                amariStr[i] = j + "個(" + (result.remainderSum[i][j]*100).toPrecision(3) + "%) " + amariStr[i];
              };
            };
            if (amariStr[i] == "") amariStr[i] = "余りなし";
          };
          result.note = "キットが余る確率"
                      + "<br>R：" + amariStr[0]
                      + "<br>SR：" + amariStr[1]
                      + "<br>SSR：" + amariStr[2]
                      + "<br><br>";
        });
        //time400sum += performance.now() - time400;

        _this.mMcMaintainSort(0, 0); // 並び順再設定
      };

      //console.log("100:" + time100sum);
      //console.log("200:" + time200sum);
      //console.log("300:" + time300sum);
      //console.log(" 350:" + time350sum);
      //console.log("  351:" + time351sum);
      //console.log("  352:" + time352sum);
      //console.log("  353:" + time353sum);
      //console.log("  354:" + time354sum);
      //console.log("  355:" + time355sum);
      //console.log(" 360:" + time360sum);
      //console.log("400:" + time400sum);

      //_this.mMcCalcMaintainEx();
    },

    mMcChangeDoll() { // お手入れ確率計算：お手入れ対象人形の設定変更
      let _this = this;

      // 蓄積済み経験値を整数化
      _this.mcDollExpOfStep = Math.floor(_this.mcDollExpOfStep);

      // 蓄積済み経験値の入力値が上限以上になっている場合は上限-100にする
      if (_this.mcDollExpOfStep >= _this.MC_EXPERIENCE_DOLL[_this.mcDollId].exp) {
        _this.mcDollExpOfStep = _this.MC_EXPERIENCE_DOLL[_this.mcDollId].exp - 100;
      };

      // 蓄積済み経験値の入力値がマイナスになっている場合は0にする
      if (_this.mcDollExpOfStep < 0) {
        _this.mcDollExpOfStep = 0;
      };

      // 蓄積経験値(入力された段階 × 段階ごとの経験値 + 入力された経験値)
      _this.mcDollExperience = _this.mcDollStep * _this.MC_EXPERIENCE_DOLL[_this.mcDollId].exp + parseInt(_this.mcDollExpOfStep);

      // 確率計算を実行
      _this.mMcCalcMaintain();
    },

    mMcGetKitName(id) { // お手入れ確率計算：お手入れキット名称取得
      if (id == 0) {
        return "R";
      } else if (id == 1) {
        return "SR";
      } else {
        return "SSR";
      };
    },

    mMcGetSuccessName(id) { // お手入れ確率計算：成功・大成功名称取得
      if (id == 0) {
        return "成功";
      } else {
        return "大成功";
      };
    },

    mMcGetProbability(kitId, successId, experienceNow) { // お手入れ確率計算：確率取得
      let _this = this;
      let probability = 1; // 確率
      let step = Math.floor(experienceNow / _this.MC_EXPERIENCE_DOLL[_this.mcDollId].exp);

      // 確率表から大成功確率を取得
      if (_this.mcDollId == 0) {
        probability *= (_this.MC_PROBABILITY_DOOL_R[kitId][step] / 100);
      } else {
        probability *= (_this.MC_PROBABILITY_DOOL_SR[kitId][step] / 100);
      };

      // 判定が成功の場合は確率を反転
      if (successId == 0) {
        probability = 1 - probability;
      };
      
      return probability;
    },

    mMcGetExperience(kitId, successId, experienceBefore) { // お手入れ確率計算：獲得経験値取得
      let _this = this;
      let exp5 = _this.MC_EXPERIENCE_DOLL[_this.mcDollId].exp * 5;  // 5段階分の経験値
      let experienceAfter = 0;  // 経験値獲得後の経験値蓄積値
      
      // 成功・大成功判定
      if (successId == 0) {
        experienceAfter = experienceBefore + _this.MC_EXPERIENCE_KIT[kitId];  // 成功時はキット経験値を加算
      } else {
        experienceAfter = experienceBefore + exp5;  // 大成功時は5段階分の経験値を加算
      };

      // 5,10,15段階到達時は超過分の経験値を削除
      if ((experienceBefore < (exp5 * 1)) && (experienceAfter > (exp5 * 1))) {
        experienceAfter = exp5 * 1;
      } else if ((experienceBefore < (exp5 * 2)) && (experienceAfter > (exp5 * 2))) {
        experienceAfter = exp5 * 2;
      } else if (experienceAfter > (exp5 * 3)) {
        experienceAfter = exp5 * 3;
      };
      
      return experienceAfter;
    },

    mMcCombinationDoll(lngKitR, lngKitSR, lngKitSSR, strUse, lngDeep, lngDeepMax) { // お手入れ確率計算：所持しているR,SR,SSRキットの組合せ
      let _this = this;
      
      if (lngDeep < lngDeepMax) {
        if (lngKitR >= 1) {
          if (strUse != "" && !strUse.endsWith("→")) strUse += "→";
          _this.mMcCombinationDoll(lngKitR - 1, lngKitSR, lngKitSSR, strUse + "R", lngDeep + 1, lngDeepMax);
        };
        if (lngKitSR >= 1) {
          if (strUse != "" && !strUse.endsWith("→")) strUse += "→";
          _this.mMcCombinationDoll(lngKitR, lngKitSR - 1, lngKitSSR, strUse + "SR", lngDeep + 1, lngDeepMax);
        };
        if (lngKitSSR >= 1) {
          if (strUse != "" && !strUse.endsWith("→")) strUse += "→";
          _this.mMcCombinationDoll(lngKitR, lngKitSR, lngKitSSR - 1, strUse + "SSR", lngDeep + 1, lngDeepMax);
        };
      } else {
        let newResult = JSON.parse(JSON.stringify(_this.mcResult));
        newResult.id = _this.mcResultList.length + 1;
        newResult.title = strUse;
        newResult.detailList.splice(0);
        //for (let i=0; i<=2; ++i) {
        //  for (let j=0; j<=10; ++j) {
        //    newResult.remainderSum[i][j] = 0;
        //  };
        //};
        strUse.split("→").forEach(function(use){
          if (use == "R") newResult.patternList.push(0);
          if (use == "SR") newResult.patternList.push(1);
          if (use == "SSR") newResult.patternList.push(2);
        });
        _this.mcResultList.push(newResult);
      };
    },

    mMcCombinationSuccess(strUse, lngDeep, lngDeepMax) { // お手入れ確率計算：成功失敗組合せ
      let _this = this;

      if (lngDeep < lngDeepMax) {
        if (strUse != "" && !strUse.endsWith(",")) strUse += ",";
        _this.mMcCombinationSuccess(strUse + "0", lngDeep + 1, lngDeepMax);
        _this.mMcCombinationSuccess(strUse + "1", lngDeep + 1, lngDeepMax);
      } else {
        _this.mcResultList.forEach(function(result){
          let newDetail = JSON.parse(JSON.stringify(_this.mcDetail));
          newDetail.id = result.detailList.length + 1;
          ////newDetail.note = "";
          newDetail.successList = strUse.split(",");
          newDetail.patternList = result.patternList;
          newDetail.experience = parseInt(_this.mcDollExperience); // 初期経験値
          ////for (let i=0; i<=2; ++i) {
          ////  for (let j=0; j<=10; ++j) {
          ////    newDetail.remainder[i][j] = 0;
          ////  };
          ////};
          result.detailList.push(newDetail);
        });
      };
    },

    mMcMaintainSort(orgIdx, upDown) { // お手入れ確率計算：並び順変更
      let _this = this;
      let cnt = 0;
      let idx = 0;
      let flgExec = 0;

      // クリックされたキーの配列番号を取得
      _this.mcSortKey.forEach(function(maintainSort){
        if(maintainSort.Idx == orgIdx) {
          idx = cnt;
        } else {
          ++cnt;
        };
      });

      _this.mcSortKey.forEach(function(maintainSort){
        if(flgExec == 0) {
          if(maintainSort.SortNum == (Number(_this.mcSortKey[idx].SortNum) + Number(upDown))){
            maintainSort.SortNum = Number(maintainSort.SortNum) + (Number(upDown) * (-1));
            flgExec = 1;
          };
        };
      });
      _this.mcSortKey[idx].SortNum = Number(_this.mcSortKey[idx].SortNum) + Number(upDown);

      // ニケ並び順配列を並び替え
      _this.mcSortKey.sort((a,b) => {
        if (a.SortNum > b.SortNum) { return 1 }
        if (a.SortNum < b.SortNum) { return -1 }
      });

      // 並び順付け替え＆保存
      let num = 1;
      _this.mcSortKey.forEach(function(maintainSort){
        maintainSort.SortNum = num++;
        localStorage.setItem('mcSortKey['+maintainSort.Idx+']', maintainSort.SortNum);
      });

      // 一覧を並び替え
      _this.mcResultList.forEach(function(result){
        result.sortKey = "";
        _this.mcSortKey.forEach(function(maintainSort){
          if(maintainSort.Name == "パターン") {
            result.sortKey += String(result.id).padStart(5, "0");
          } else if(maintainSort.Name == "期待値") {
            result.sortKey += String(10000000000 - result.expectedSum.toFixed(5)*100000).padStart(15, "0");
          } else if(maintainSort.Name == "平均値") {
            result.sortKey += String(10000000000 - result.average.toFixed(5)*100000).padStart(15, "0");
          } else if(maintainSort.Name == "中央値") {
            result.sortKey += String(10000000000 - result.median.toFixed(5)*100000).padStart(15, "0");
          } else if(maintainSort.Name == "15段階到達確率") {
            result.sortKey += String(10000000000 - result.probabilityMaximum.toFixed(5)*100000).padStart(15, "0");
          };
        });
      });
      
      // ソート実行
      _this.mcResultList.sort((a,b) => {
        if (a.sortKey > b.sortKey) { return 1 }
        if (a.sortKey < b.sortKey) { return -1 }
      });
    },

    mCoMouseOverMaintainSort(orgIdx) { // お手入れ確率計算：並び順変更ボタンマウスオーバー
      let _this = this;
      let cnt = 0;
      let idx1 = 0;
      let idx2 = 0;

      // マウスオーバーされたキーの配列番号を取得
      cnt = 0;
      _this.mcSortKey.forEach(function(maintainSort){
        if(maintainSort.Idx == orgIdx) {
          idx1 = cnt;
        } else {
          ++cnt;
        };
      });

      // マウスオーバーされたキーの1つ下のキーの配列番号を取得
      cnt = 0;
      _this.mcSortKey.forEach(function(maintainSort){
        if((maintainSort.SortNum - 1) == _this.mcSortKey[idx1].SortNum) {
          idx2 = cnt;
        } else {
          ++cnt;
        };
      });

      // フラグを全クリア
      _this.mcSortKey.forEach(function(maintainSort){
        maintainSort.MouseOverFlg = false;
      });

      // フラグをセット
      _this.mcSortKey[idx1].MouseOverFlg = true;
      _this.mcSortKey[idx2].MouseOverFlg = true;
    },

    mCoMouseLeaveMaintainSort() { // お手入れ確率計算：並び順変更ボタンマウスリーブ
      let _this = this;
      // フラグを全クリア
      _this.mcSortKey.forEach(function(maintainSort){
        maintainSort.MouseOverFlg = false;
      });
    },

    mSfChangeDisp(key) { // お手入れ確率計算：表示On/Off
      if(this.mcDisp[key] == 0) {
        this.mcDisp[key] = 1;
      } else {
        this.mcDisp[key] = 0;
      }
      localStorage.setItem('mcDisp[' + key + ']', this.mcDisp[key]);
      this.mMcCalcMaintain(); // お手入れ確率計算再実施
    },

