    mSelectTab(idx) {
      let _this = this;
      localStorage.setItem('coSelectTab', idx);
      _this.mSetting(0); // 設定画面を閉じる
      _this.coSelTab = idx;
      _this.mReset(); // パック一覧の絞り込みをクリア
      _this.mClearQfFil(); // ニケの絞り込みをクリア

      if      (_this.coSelTab == 1) _this.isFilter = true;
      else if (_this.coSelTab == 2) _this.isFilter = false;
      else if (_this.coSelTab == 3) _this.isFilter = true;
      else if (_this.coSelTab == 4) _this.isFilter = true;
      else if (_this.coSelTab == 5) _this.isFilter = true;
      else if (_this.coSelTab == 6) _this.isFilter = false;
      else if (_this.coSelTab == 7) _this.isFilter = true;
    },

    mLsPackCheck(id, chkOn) { // 育成：課金パック選択
      let _this = this;
      this.packs.forEach(function(pack){
        if(pack.id == id) {
          pack.check = pack.check * (-1);

          if (chkOn) {
            if (_this.selected[pack.id] === undefined) {
              _this.selected[pack.id] = true;
            } else {
              _this.selected[pack.id] = !_this.selected[pack.id];
            }
          }
        }
      })
      this.mCalcLeveling();
    },

    mCalcLeveling() { // 育成：レベリング計算
      let _this = this;

      _this.needs[0].NextLv = _this.coNikkeLv + 1;
      _this.needs[1].NextLv = Math.floor(_this.coNikkeLv / 20) * 20 + 21;
      _this.needs[2].NextLv = _this.needs[1].NextLv + 20;

      _this.lsPosCaseCredit[0] = parseInt(_this.lsPosCaseCredit[1])
                               + parseInt(_this.lsPosCaseCredit[2]) * 2
                               + parseInt(_this.lsPosCaseCredit[3]) * 8
                               + parseInt(_this.lsPosCaseCredit[4]) * 12
                               + parseInt(_this.lsPosCaseCredit[5]) * 24
                               + parseInt(_this.lsPosCaseCredit[6]) * 10;

      _this.lsPosCaseBattle[0] = parseInt(_this.lsPosCaseBattle[1])
                               + parseInt(_this.lsPosCaseBattle[2]) * 2
                               + parseInt(_this.lsPosCaseBattle[3]) * 8
                               + parseInt(_this.lsPosCaseBattle[4]) * 12
                               + parseInt(_this.lsPosCaseBattle[5]) * 24
                               + parseInt(_this.lsPosCaseBattle[6]) * 10;
      _this.lsPosCaseCore[0] = parseInt(_this.lsPosCaseCore[1])
                             + parseInt(_this.lsPosCaseCore[2]) * 2
                             + parseInt(_this.lsPosCaseCore[3]) * 8
                             + parseInt(_this.lsPosCaseCore[4]) * 12
                             + parseInt(_this.lsPosCaseCore[5]) * 24
                             + parseInt(_this.lsPosCaseCore[6]) * 10;

      // リスト上でチェックされたパックから追加分の資源を読み取り
      let creditDayNum = 0;
      let creditWeekNum = 0;
      let creditMonthNum = 0;
      let creditDayCase = 0;
      let creditWeekCase = 0;
      let creditMonthCase = 0;
      let coreDayNum = 0;
      let coreWeekNum = 0;
      let coreMonthNum = 0;
      let coreDayCase = 0;
      let coreWeekCase = 0;
      let coreMonthCase = 0;
      let battleDayNum = 0;
      let battleWeekNum = 0;
      let battleMonthNum = 0;
      let battleDayCase = 0;
      let battleWeekCase = 0;
      let battleMonthCase = 0;
      _this.packs.forEach(function(pack){
        if (pack.check == true) {
          if (pack.division == "デイリー") {
            creditDayCase += pack.items.CaseCredit;
            creditDayNum += pack.items.Credit * 1000;
            coreDayCase += pack.items.CaseCore;
            coreDayNum += pack.items.Core;
            battleDayCase += pack.items.CaseBattle;
            battleDayNum += pack.items.Battle * 1000;
          } else if (pack.division == "ウィークリー") {
            creditWeekCase += pack.items.CaseCredit;
            creditWeekNum += pack.items.Credit * 1000;
            coreWeekCase += pack.items.CaseCore;
            coreWeekNum += pack.items.Core;
            battleWeekCase += pack.items.CaseBattle;
            battleWeekNum += pack.items.Battle * 1000;
          } else if (pack.division == "マンスリー") {
            creditMonthCase += pack.items.CaseCredit;
            creditMonthNum += pack.items.Credit * 1000;
            coreMonthCase += pack.items.CaseCore;
            coreMonthNum += pack.items.Core;
            battleMonthCase += pack.items.CaseBattle;
            battleMonthNum += pack.items.Battle * 1000;
          } else if (pack.name == "30-DAY成長補給品") {
            coreDayCase += 10;
          }
        }
      });

      // 経験値リストから現在レベルの蓄積経験値と目標レベルの蓄積経験値を取得
      var expNow = _this.experience.find(e => e.Lv === _this.coNikkeLv);

      for (let i=0; i <= 2; ++i) {
        if (this.needs[i].NextLv <= this.experience.length) {
          let expNext = this.experience.find(e => e.Lv === this.needs[i].NextLv);
          this.needs[i].NeedCredit = expNext.Credit - expNow.Credit - (this.lsPosCaseCredit[0] * this.coRewardCredit) - (this.lsPosCredit * 1000);
          this.needs[i].NeedBattle = expNext.Battle - expNow.Battle - (this.lsPosCaseBattle[0] * this.coRewardBattle) - (this.lsPosBattle * 1000);
          this.needs[i].NeedCore   = expNext.Core   - expNow.Core   - (this.lsPosCaseCore[0]   * this.coRewardCore)   - this.lsPosCore;
        }
      }

      // クレジットの所要時間（次レベルまでの残り時間を計算）
      let flgCredit = false;
      let flgBattle = false;
      let flgCore = false;
      let lpCnt = -1;
      if (this.needs[0].NextLv <= this.experience.length) lpCnt++; // 次のレベルがデータテーブルの範囲内
      if (this.needs[1].NextLv <= this.experience.length) lpCnt++; // 20レベル区切りの次のレベルがデータテーブルの範囲内
      if (this.needs[2].NextLv <= this.experience.length) lpCnt++; // 20レベル区切りの次の次のレベルがデータテーブルの範囲内

      for (let i = 0; i <= lpCnt; ++i) {
        let nextday = new Date();
        let nextdayStr = "";
        let preCredit = this.needs[i].NeedCredit;
        let preBattle = this.needs[i].NeedBattle;
        let preCore = this.needs[i].NeedCore;

        this.needs[i].HourCredit = 0;
        this.needs[i].HourBattle = 0;
        this.needs[i].HourCore = 0;
        this.needs[i].NeedYen = 0;
        
        this.needs[i].LogCredit = "";
        this.needs[i].LogCredit += "★クレジット";
        this.needs[i].LogCredit += "<br>＜必要量＞";
        this.needs[i].LogCredit += "<br>　Lv" + this.coNikkeLv + "⇒" + this.needs[i].NextLv + "　" + Math.floor((this.needs[i].NeedCredit + (this.lsPosCaseCredit[0] * this.coRewardCredit) + Number(this.lsPosCredit * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>＜収入＞";
        this.needs[i].LogCredit += "<br>　所持　ケース+個別：" + Math.floor(((this.lsPosCaseCredit[0] * this.coRewardCredit) + (this.lsPosCredit * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>　毎日　防御報酬：" + Math.floor(Math.floor(this.coRewardCredit*24)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "、殲滅+ミッション：" + Math.floor((Math.floor(this.lsWipeouts * this.coRewardCredit * 2) + (1 * Math.floor(this.coRewardCredit)))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "、課金：" + Math.floor((creditDayCase * Math.floor(this.coRewardCredit) + creditDayNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>　毎週　ミッション：" + Math.floor(Math.floor((8 * this.coRewardCredit) * 2)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "、鉄くず：" + Math.floor((18 * Math.floor(this.coRewardCredit) + 500000)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "、課金：" + Math.floor((creditWeekCase * Math.floor(this.coRewardCredit) + creditWeekNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>　毎月　課金：" + Math.floor((creditMonthCase * Math.floor(this.coRewardCredit) + creditMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>＜目標レベルまでの必要量推移＞";
        this.needs[i].LogCredit += "<br>　" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")";
        this.needs[i].LogCredit += "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K";

        this.needs[i].LogBattle = "";
        this.needs[i].LogBattle += "★バトルデータ";
        this.needs[i].LogBattle += "<br>＜必要量＞";
        this.needs[i].LogBattle += "<br>　Lv" + this.coNikkeLv + "⇒" + this.needs[i].NextLv + "　" + Math.floor((this.needs[i].NeedBattle + (this.lsPosCaseBattle[0] * this.coRewardBattle) + Number(this.lsPosBattle * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>＜収入＞";
        this.needs[i].LogBattle += "<br>　所持　ケース+個別：" + Math.floor(((this.lsPosCaseBattle[0] * this.coRewardBattle) + (this.lsPosBattle * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>　毎日　防御報酬：" + Math.floor(this.coRewardBattle*24/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "、殲滅+シミュレーション：" + Math.floor((Math.floor(this.lsWipeouts * this.coRewardBattle * 2) + (18 * Math.floor(this.coRewardBattle)))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "、課金：" + Math.floor((battleDayCase * Math.floor(this.coRewardBattle) + battleDayNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>　毎週　鉄くず：" + Math.floor((36 * Math.floor(this.coRewardBattle))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "、課金：" + Math.floor((battleMonthCase * Math.floor(this.coRewardBattle) + battleMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>　毎月　課金：" + Math.floor((battleMonthCase * Math.floor(this.coRewardBattle) + battleMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>＜目標レベルまでの必要量推移＞";
        this.needs[i].LogBattle += "<br>　" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")";
        this.needs[i].LogBattle += "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K";

        this.needs[i].LogCore = "";
        this.needs[i].LogCore += "★コアダスト";
        this.needs[i].LogCore += "<br>＜必要量＞";
        this.needs[i].LogCore += "<br>　Lv" + this.coNikkeLv + "⇒" + this.needs[i].NextLv + "　" + Math.floor(this.needs[i].NeedCore + (this.lsPosCaseCore[0] * this.coRewardCore) + Number(this.lsPosCore)).toLocaleString();
        this.needs[i].LogCore += "<br>＜収入＞";
        this.needs[i].LogCore += "<br>　所持　ケース+個別：" + Math.floor((this.lsPosCaseCore[0] * this.coRewardCore) + this.lsPosCore).toLocaleString();
        this.needs[i].LogCore += "<br>　毎日　防御報酬：" + Math.floor(this.coRewardCore*24).toLocaleString();
        this.needs[i].LogCore += "、殲滅+ミッション：" + (Math.floor(this.lsWipeouts * this.coRewardCore * 2) + 50).toLocaleString();
        this.needs[i].LogCore += "、課金：" + (coreDayCase * Math.floor(this.coRewardCore) + coreDayNum).toLocaleString();
        this.needs[i].LogCore += "<br>　毎週　鉄くず：" + (36 * Math.floor(this.coRewardCore)).toLocaleString();
        this.needs[i].LogCore += "、課金：" + (coreWeekCase * Math.floor(this.coRewardCore) + coreWeekNum).toLocaleString();
        this.needs[i].LogCore += "<br>　毎月　課金：" + (coreMonthCase * Math.floor(this.coRewardCore) + coreMonthNum).toLocaleString();
        this.needs[i].LogCore += "<br>＜目標レベルまでの必要量推移＞";
        this.needs[i].LogCore += "<br>　" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")";
        this.needs[i].LogCore += "　" + Math.floor(this.needs[i].NeedCore).toLocaleString();

        while (this.needs[i].NeedCredit > 0 || this.needs[i].NeedBattle > 0 || this.needs[i].NeedCore > 0) {
          nextday.setHours(nextday.getHours() + 1); // +1h
          nextdayStr = nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")"
          flgCredit = false;
          flgBattle = false;
          flgCore = false;

          // クレジット計算
          if (this.needs[i].NeedCredit > 0) {
            flgCredit = true;
            this.needs[i].HourCredit++; // 残り時間を+1
            this.needs[i].NeedCredit -= this.coRewardCredit; // 必要経験値から1時間分の基地防御報酬を引く
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditDayCase * Math.floor(this.coRewardCredit)) // デイリー系課金アイテム(ケース)
                                         + creditDayNum // デイリー系課金アイテム(個)
                                         + Math.floor(this.lsWipeouts * this.coRewardCredit * 2) // まとめて殲滅
                                         + (1 * Math.floor(this.coRewardCredit)); // デイリーミッション
              this.needs[i].LogCredit += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K　デイリー（" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K）";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedCredit > 0) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditWeekCase * Math.floor(this.coRewardCredit)) // ウィークリー系課金アイテム(ケース)
                                        + creditWeekNum // ウィークリー系課金アイテム(個)
                                        + Math.floor((8 * this.coRewardCredit)) * 2 // ウィークリーミッション
                                        + (18 * Math.floor(this.coRewardCredit) + 500000); // 鉄くずショップ
              this.needs[i].LogCredit += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K　ウィークリー（" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K）";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedCredit > 0) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditMonthCase * Math.floor(this.coRewardCredit)) // マンスリー系課金アイテム(ケース)
                                        + creditMonthNum; // マンスリー系課金アイテム(個)
              this.needs[i].LogCredit += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K　マンスリー（" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K）";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedCredit <= 0) {
              this.needs[i].LogCredit += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K　デイリー（" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K）";
            }
          }
          
          // バトルデータ計算
          if (this.needs[i].NeedBattle > 0) {
            flgBattle = true;
            this.needs[i].HourBattle++; // 残り時間を+1
            this.needs[i].NeedBattle -= this.coRewardBattle; // 必要経験値から1時間分の基地防御報酬を引く
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleDayCase * Math.floor(this.coRewardBattle)) // デイリー系課金アイテム(ケース)
                                        + battleDayNum // デイリー系課金アイテム(個)
                                        + Math.floor(this.lsWipeouts * this.coRewardBattle * 2) // まとめて殲滅
                                        + (18 * Math.floor(this.coRewardBattle)); // シミュレーションルーム5-Cクリア
              this.needs[i].LogBattle += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K　デイリー（" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K）";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedBattle > 0) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleWeekCase * Math.floor(this.coRewardBattle)) // ウィークリー系課金アイテム(ケース)
                                        + battleWeekNum // デイリー系課金アイテム(個)
                                        + (36 * Math.floor(this.coRewardBattle)); // 鉄くずショップ
              this.needs[i].LogBattle += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K　ウィークリー（" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K）";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedBattle > 0) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleMonthCase * Math.floor(this.coRewardBattle)) // マンスリー系課金アイテム(ケース)
                                        + battleMonthNum; // マンスリー系課金アイテム(個)
              this.needs[i].LogBattle += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K　マンスリー（" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K）";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedBattle <= 0) {
              this.needs[i].LogBattle += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K　デイリー（" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K）";
            }
          }
          
          // コアダスト計算
          if (this.needs[i].NeedCore > 0) {
            flgCore = true;
            this.needs[i].HourCore++; // 残り時間を+1
            this.needs[i].NeedCore -= this.coRewardCore; // 必要経験値から1時間分の基地防御報酬を引く
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreDayCase * Math.floor(this.coRewardCore)) // デイリー系課金アイテム(ケース)
                                      + coreDayNum // デイリー系課金アイテム(個)
                                      + Math.floor(this.lsWipeouts * this.coRewardCore * 2) // まとめて殲滅
                                      + 50; // デイリーミッション
              this.needs[i].LogCore += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "　デイリー（" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "）";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedCore > 0) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreWeekCase * Math.floor(this.coRewardCore)) // ウィークリー系課金アイテム(ケース)
                                      + coreWeekNum // デイリー系課金アイテム(個)
                                      + (36 * Math.floor(this.coRewardCore)) // 鉄くずショップ
              this.needs[i].LogCore += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "　ウィークリー（" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "）";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedCore > 0) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreMonthCase * Math.floor(this.coRewardCore)) // マンスリー系課金アイテム(ケース)
                                      + coreMonthNum; // マンスリー系課金アイテム(個)
              this.needs[i].LogCore += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "　マンスリー（" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "）";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedCore <= 0) {
              this.needs[i].LogCore += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "　デイリー（" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "）";
            }
          }

          // 課金額計算
          if (nextday.getHours() == 5) {
            if (flgCredit || flgBattle || flgCore) {
              //if (this.needs[i].NeedCredit >= 0 && this.needs[i].NeedBattle >= 0 && this.needs[i].NeedCore >= 0) {
                this.packs.forEach(function(pack){
                  if (pack.check == true) {
                    // 5時を迎えた時
                    if (nextday.getHours() == 5 && pack.division == "デイリー") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }

                    // 火曜の5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDay() == 2 && pack.division == "ウィークリー") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                    
                    // 月初めの5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDate() == 1 && pack.division == "マンスリー") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                    
                    // 月初めの5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDate() == 1 && pack.name == "30-DAY成長補給品") {
                      if (flgCore) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                  }
                })
              //}
            }
          }
        };

        // 所要日数
        this.needs[i].DayCredit = this.needs[i].HourCredit / 24;
        this.needs[i].DayBattle = this.needs[i].HourBattle / 24;
        this.needs[i].DayCore   = this.needs[i].HourCore / 24;

        // 所要日数グラフ
        this.needs[i].DayCreditPer = Math.floor(this.needs[i].DayCredit / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)
        this.needs[i].DayBattlePer = Math.floor(this.needs[i].DayBattle / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)
        this.needs[i].DayCorePer   = Math.floor(this.needs[i].DayCore   / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)

        // 最短日数
        let today = new Date();
        let maxHour = Math.max(this.needs[i].HourCredit, this.needs[i].HourBattle, this.needs[i].HourCore);
        today.setHours(today.getHours() + maxHour);
        this.needs[i].ArrivalDate = (today.getFullYear()+"").slice(-2) + "/" + ("00"+(today.getMonth()+1)).slice(-2) + "/" + ("00"+today.getDate()).slice(-2) + "<br>(" + [ "日", "月", "火", "水", "木", "金", "土" ][today.getDay()] + ")<br>" + ("00"+today.getHours()).slice(-2) + "時";

        // 現在日時
        this.lsToday = new Date();
        this.lsToday = "現在：" + (this.lsToday.getFullYear()+"").slice(-2) + "/" + ("00"+(this.lsToday.getMonth()+1)).slice(-2) + "/" + ("00"+this.lsToday.getDate()).slice(-2) + "(" + [ "日", "月", "火", "水", "木", "金", "土" ][this.lsToday.getDay()] + ")" + ("00"+this.lsToday.getHours()).slice(-2) + "時";
      }

      for (let i = lpCnt+1; i <= 2; ++i) {
        // 所要日数
        this.needs[i].HourCredit = 0;
        this.needs[i].HourBattle = 0;
        this.needs[i].HourCore = 0;
        this.needs[i].DayCredit = 0;
        this.needs[i].DayBattle = 0;
        this.needs[i].DayCore = 0;
        this.needs[i].DayCreditPer = 0;
        this.needs[i].DayBattlePer = 0;
        this.needs[i].DayCorePer = 0;
        this.needs[i].ArrivalDate = "必要資材<br>データ無し";
        this.needs[i].NeedYen = 0;
      }

    },

    mChangeNikkeLv() { // ニケレベル変更
      if (this.coNikkeLv < 1) {
        this.coNikkeLv = 1;
      } else if (this.coNikkeLv > this.experience.length) {
        this.coNikkeLv = this.experience.length;
      } else {
        this.coNikkeLv = Math.floor(this.coNikkeLv);
      }
      localStorage.setItem('coNikkeLv', this.coNikkeLv);
      this.mCalcLeveling();
    },

    mChangeRewardLv() { // 基地防御レベル変更
      if (this.coRewardLv < 1) {
        this.coRewardLv = 1;
      } else if (this.coRewardLv > this.reward.length) {
        this.coRewardLv = this.reward.length;
      } else {
        this.coRewardLv = Math.floor(this.coRewardLv);
      }
      var found = this.reward.find(e => e.Lv === this.coRewardLv);
      this.coRewardCredit = Math.floor(found.Credit*1.6);
      this.coRewardBattle = Math.floor(found.Battle*1.45);
      this.coRewardCore = Math.floor(found.Core*1.45 * Math.pow(10,2)) / Math.pow(10,2);
      localStorage.setItem('coRewardLv', this.coRewardLv);
      localStorage.setItem('coRewardCredit', this.coRewardCredit);
      localStorage.setItem('coRewardBattle', this.coRewardBattle);
      localStorage.setItem('coRewardCore', this.coRewardCore);
      this.mCalcLeveling();
    },

    mChangeWipeouts() { // まとめて殲滅回数変更
      if (this.lsWipeouts < 1) {
        this.lsWipeouts = 1;
      } else if (this.lsWipeouts > 11) {
        this.lsWipeouts = 11;
      } else {
        this.lsWipeouts = Math.floor(this.lsWipeouts);
      }
      //this.$cookies.set('lsWipeouts', this.lsWipeouts, 0);
      localStorage.setItem('lsWipeouts', this.lsWipeouts);
      this.mCalcLeveling();
    },

    mChangePosCaseCredit(idx) { // クレジットケース数量変更
