    mSkillInfoClose(skillId, nikkeId) { // 部隊編成 - スキル詳細を消す     
      // 絞り込み対象スキル情報の配列から×ボタンが押されたものを消す
      let cnt = 0;
      let idx = 0;
      this.skillInfos.forEach(function(skillInfo){
        if(skillInfo.id == skillId) {
          //alert("remove:" + cnt)
          idx = cnt;
        }
        cnt++;
      });
      this.skillInfos.splice(idx, 1);

      // 部隊欄に編成済みのニケではない場合、画像を非表示化
      if (this.coSelTab == 3) {
        if (!document.getElementById(nikkeId).parentNode.id.startsWith('sfSquad')) {
          document.getElementById(nikkeId).style.display = "none";
        };
      } else {
        document.getElementById(nikkeId).style.display = "none";
      }
    },

    mGetSkillMarkLine(skillText, reg) {
      let spl = skillText.split("<br>");
      let ret = "";
      for (let i=0; i<spl.length; ++i) {
        if (ret != "") ret += "<br>";
        ret += spl[i].replace(reg, '<mark>$1</mark>');
      };
      return ret;
    },

    mGetSkillMarkSentence (skillText, reg) {
      let spl = skillText.split("<br>");
      let ret = "";
      let record = [];
      let flgFind = false;

      for (let i=0; i<spl.length; ++i) {
        if (spl[i].startsWith("・")) {
          if (ret != "") ret += "<br>";
          ret += spl[i];
        } else {
          if (ret.includes("<mark>") && reg.test(ret)) {
            ret = ret.replace(reg, '<mark>$1</mark>');
            flgFind = true;
          };
          if (ret != "") record.push(ret);
          ret = spl[i];
        };
      };
      
      // 最後の行を含む文章の処理
      if (ret != "") {
        if (ret.includes("<mark>") && reg.test(ret)) {
          ret = ret.replace(reg, '<mark>$1</mark>');
          flgFind = true;
        };
        record.push(ret);
        ret = "";
      };
      
      for (let i=0; i<record.length; ++i) {
        if (ret != "") ret += "<br>";
        ret += record[i];
      };

      if (flgFind) {
        return ret;
      } else {
        return "";
      };
    },

