                              , _this.pManualBurst1 * 180
                              , _this.pManualBurst2 * 125
                              , _this.pManualBurst3 * 50
                              ) * pack.items.CustomPack2 // カスタムパックⅡ(選択枠3)
                    
                    + Math.max(_this.pCompanyArms * 200
                              , _this.pCaseCredit * 50 + _this.pCaseCore * 50 + _this.pCaseBattle * 50
                              , _this.pCaseCredit * 300
                              , _this.pCaseCore * 100
                              , _this.pCaseBattle * 100
                              ) * pack.items.CustomPack3 // カスタムパックⅢ(選択枠1)
                    + Math.max(_this.pCustomModule * 3
                              , _this.pManualSkill1 * 300
                              , _this.pManualSkill2 * 220
                              , _this.pManualBurst1 * 150
                              , _this.pManualBurst2 * 110
                              ) * pack.items.CustomPack3 // カスタムパックⅢ(選択枠2)
                    + Math.max(_this.pManualSkill1 * 360
                              , _this.pManualSkill2 * 250
                              , _this.pManualSkill3 * 100
                              , _this.pManualBurst1 * 180
                              , _this.pManualBurst2 * 125
                              , _this.pManualBurst3 * 50
                              ) * pack.items.CustomPack3 // カスタムパックⅢ(選択枠3)
                    + Math.max(_this.pTicketRegular * 30
                              , _this.pEquipmentT9 * 1
                              ) * pack.items.CustomPack3 // カスタムパックⅢ(選択枠4)

                    + Math.max(_this.pManualSkill2 * 140
                              , _this.pManualBurst2 * 70
                              , _this.pManualSkill1 * 220
                              , _this.pManualBurst1 * 100
                              , _this.pCaseCore * 60
                              , _this.pCaseBattle * 60
                              , _this.pCaseCredit * 180
                              ) * pack.items.CustomPackS1 // カスタムパックSI(選択枠1)
                    + Math.max(_this.pManualSkill2 * 140
                              , _this.pManualBurst2 * 70
                              , _this.pManualSkill1 * 220
                              , _this.pManualBurst1 * 100
                              , _this.pCaseCore * 60
                              , _this.pCaseBattle * 60
                              , _this.pCaseCredit * 180
                              ) * pack.items.CustomPackS1 // カスタムパックSI(選択枠2)
                    
                    + Math.max(_this.pCompanyArms * 200
                              , _this.pManualSkill2 * 300
                              , _this.pManualSkill1 * 150
                              , _this.pCaseCore * 100
                              , _this.pCaseBattle * 100
                              , _this.pCaseCredit * 300
                              ) * pack.items.CustomPackS2 // カスタムパックSII(選択枠1)
                    + Math.max(_this.pCustomModule * 2
                              , _this.pManualSkill3 * 80
                              , _this.pManualBurst3 * 40
                              , _this.pManualSkill2 * 200
                              , _this.pManualBurst2 * 100
                              ) * pack.items.CustomPackS2 // カスタムパックSII(選択枠2)
                    + Math.max(_this.pManualSkill3 * 100
                              , _this.pManualBurst3 * 50
                              , _this.pManualSkill2 * 250
                              , _this.pManualBurst2 * 125
                              , _this.pManualSkill1 * 360
                              , _this.pManualBurst1 * 180
                              ) * pack.items.CustomPackS2 // カスタムパックSII(選択枠3)

                    + Math.max(_this.pCompanyArms * 200
                              , _this.pManualSkill2 * 300
                              , _this.pManualSkill1 * 150
                              , _this.pCaseCore * 100
                              , _this.pCaseBattle * 100
                              , _this.pCaseCredit * 300
                              ) * pack.items.CustomPackS3 // カスタムパックSIII(選択枠1)
                    + Math.max(_this.pCustomModule * 3
                              , _this.pManualSkill3 * 88
                              , _this.pManualBurst3 * 44
                              , _this.pManualSkill2 * 220
                              , _this.pManualBurst2 * 110
                              ) * pack.items.CustomPackS3 // カスタムパックSIII(選択枠2)
                    + Math.max(_this.pManualSkill3 * 100
                              , _this.pManualBurst3 * 50
                              , _this.pManualSkill2 * 250
                              , _this.pManualBurst2 * 125
                              , _this.pManualSkill1 * 360
                              , _this.pManualBurst1 * 180
                              ) * pack.items.CustomPackS3 // カスタムパックSIII(選択枠3)
                    + Math.max(_this.pTicketRegular * 30
                              , _this.pEquipmentT9 * 1
                              ) * pack.items.CustomPackS3 // カスタムパックSIII(選択枠4)
                    ;

        pack.degree = pack.myPrice / pack.price * 100;  // お得度
      })
    },

    mReset(){ // 課金シミュ絞り込みリセット
      var _this = this;
      this.divisions.forEach(function(division){
        division.check = false;
      });
      this.contents.forEach(function(content){
        content.check = false;
      });
    },

    mSortBy(key1, key2) {
      this.sort_key1 === key1 ? (this.sort_asc = !this.sort_asc) : (this.sort_asc = true)
      this.sort_key1 = key1
      this.sort_key2 = key2
    },

    mAddClass(key) {
      return {
        asc: this.sort_key1 === key && this.sort_asc,
        desc: this.sort_key1 === key && !this.sort_asc
      }
    },

    ngramify(text, set, n, padding) {
      n = n != null ? n : 3;
      if (text.length < n) {
        padding = padding != null ? padding : ' ';
        text = text + Array(n-text.length+1).join(padding);
      }

      if (n == 1 && !set) {
        set = new Set(text);
      } else {
        set = set || new Set();
        for (let i=text.length-n+1; i--; ) {
          set.add(text.substring(i, i+n));
        }
      }
      return set;
    },

    trigramify(text, set) {
      text = '  '+text.toLowerCase()+'  ';
      set = set || new Set();
      for (let i=text.length-2; i--; ) {
        set.add(text.substring(i, i+3));
      }
      return set;
    },

    engramify(text, set) {
      set = set || new Set();
      const re = /[A-Z]+[a-z]*|[A-Z]*[a-z]+|'[A-Z]*[a-z]*|[0-9]+|[^A-Za-z0-9'"!\?\-:;,\.\s]+/g;
      let m;
      while ((m=re.exec(text)) !== null) {
        if (m[0].charCodeAt(0) <= 0xFE) {
          set.add(m[0].toLowerCase());
        } else {
          trigramify(m[0], set);
        }
      }
      return set;
    },

    relevance(set1, set2) {
      let count = 0;
      set2.forEach(function(value) {
        if (set1.has(value)) {
          count = count + 1;
        }
      });
      return count / (set1.size + set2.size - count);
    },

    mMenu(idx) { // メニュー表示ON/OFF
      let _this = this;
      let menuName = "";
      let btnName = "";

      if (idx == 1)        { menuName = "bsMenu"; btnName = "bsMenuBtn"
      } else if (idx == 2) { menuName = "rfMenu"; btnName = "rfMenuBtn"
      } else if (idx == 3) { menuName = "sfMenu"; btnName = "sfMenuBtn"
      } else if (idx == 4) { menuName = "smMenu"; btnName = "smMenuBtn"
      } else if (idx == 5) { menuName = "lsMenu"; btnName = "lsMenuBtn"
      } else if (idx == 6) { menuName = "fsMenu"; btnName = "fsMenuBtn"
      } else if (idx == 7) { menuName = "tsMenu"; btnName = "tsMenuBtn"
      }

      const menu = document.getElementById(menuName);
      const menuBtn = document.getElementById(btnName);
      if (menu.style.display == "") {
        menu.style.display = "none";
        menuBtn.open = ""
        _this.coMenuOpen[idx] = false;
      } else {
        menu.style.display = ""
        menuBtn.open = "open"
        _this.coMenuOpen[idx] = true;
      };
      _this.$set(_this.coMenuOpen, idx, _this.coMenuOpen[idx]);
    },

    mSetCandidateHeight() { // 部隊編成：候補一覧高さ調整
      let _this = this;
      const squad6 = document.getElementById("sfSquad6");
      const squad6Rect = squad6.getBoundingClientRect();
      if (_this.isWidth520) {
        _this.sfCandidateHeight = window.innerHeight - (squad6Rect.bottom + document.documentElement.scrollTop) - 100;
      } else {
        _this.sfCandidateHeight = window.innerHeight - (squad6Rect.bottom + document.documentElement.scrollTop) - 50;
      };
      if (_this.sfCandidateHeight < 200) _this.sfCandidateHeight = 200;

      _this.sfScrollBerHeight = _this.sfCandidateHeight;
    },

    mSetTouchButtonHeight() { // 速射：タップボタン高さ調整
      let _this = this;
      const result = document.getElementById("rfResult");
      const resultRect = result.getBoundingClientRect();
      _this.rfTouchButtonHeight = window.innerHeight - (resultRect.bottom + document.documentElement.scrollTop) - 100;
      if (_this.rfTouchButtonHeight < 200) _this.rfTouchButtonHeight = 200;
    },

    mSetting(mode) { // 設定
      let _this = this;
      let modeShow = "";
      let menuName = "";

      if (mode == 0) {
        modeShow = "";
        _this.coSetting = false;
        _this.coFilter = false;
      } else if (mode == 1) {
        modeShow = "Setting";
        _this.coSetting = !_this.coSetting;
        _this.coFilter = false;
      } else {
        modeShow = "Filter";
        _this.coSetting = false;
        _this.coFilter = !_this.coFilter ;
      };

      if      (_this.coSelTab == 1) menuName = "bsMenu";
      else if (_this.coSelTab == 2) menuName = "rfMenu";
      else if (_this.coSelTab == 3) menuName = "sfMenu";
      else if (_this.coSelTab == 4) menuName = "smMenu";
      else if (_this.coSelTab == 5) menuName = "lsMenu";
      else if (_this.coSelTab == 6) menuName = "fsMenu";
      else if (_this.coSelTab == 7) menuName = "tsMenu";

      const menuChange = document.getElementById(menuName + modeShow);
      if (menuChange != undefined) {
        if (menuChange.classList.contains("coMenuPanelShow")) {
          menuChange.classList.remove("coMenuPanelShow");
        } else {
          menuChange.classList.add("coMenuPanelShow");
        };
      };

      if (!_this.coSetting) {
        const menuClose = document.getElementById(menuName + "Setting");
        if (menuClose != undefined) {
          if (menuClose.classList.contains("coMenuPanelShow")) {
            menuClose.classList.remove("coMenuPanelShow");
          };
        };
      };
      if (!_this.coFilter) {
        const menuClose = document.getElementById(menuName + "Filter");
        if (menuClose != undefined) {
          if (menuClose.classList.contains("coMenuPanelShow")) {
            menuClose.classList.remove("coMenuPanelShow");
          };
        };
      };
    },

    mSfScrollCandidate() { // 部隊編成：候補一覧スクロール時にスクロール量をオリジナルスクロールバーへ反映
      let _this = this;
      let candidate = document.getElementById("sfCandidate");
      let elemScrollMaxY = candidate.scrollHeight - candidate.clientHeight;
      _this.sfCandidateScroll = (candidate.scrollTop / elemScrollMaxY) * 100;
    },

    mSmScrollCandidate() { // 部隊編成：候補一覧スクロール時にスクロール量をオリジナルスクロールバーへ反映
      let _this = this;
      let candidate = document.getElementById("smCandidate");
      let elemScrollMaxY = candidate.scrollHeight - candidate.clientHeight;
      _this.smCandidateScroll = (candidate.scrollTop / elemScrollMaxY) * 100;
    },

    mSfScrollBar() { // 部隊編成：オリジナルスクロールバーによる候補一覧スクロール
      let _this = this;
      let candidate = document.getElementById("sfCandidate");
      let elemScrollMaxY = candidate.scrollHeight - candidate.clientHeight;
      candidate.scrollTop = elemScrollMaxY * (_this.sfCandidateScroll / 100);
    },

    mSmScrollBar() { // 部隊編成：オリジナルスクロールバーによる候補一覧スクロール
      let _this = this;
      let candidate = document.getElementById("smCandidate");
      let elemScrollMaxY = candidate.scrollHeight - candidate.clientHeight;
      candidate.scrollTop = elemScrollMaxY * (_this.smCandidateScroll / 100);
    },

    mSfSquadSetClick(idx) { // 部隊編成：部隊セット編集
      for (let i=1; i<=5; ++i) {
        let summamry = document.getElementById("sfSquadSetSummary" + i);
        if (summamry != undefined && i != idx) {
          summamry.parentElement.open = "";
        }
      }
      const target = document.getElementById("sfSquadSetSummary" + idx);
      target.click();
    },

    mSfCopySquadSet(summaryNo, idxTo) { // 部隊編成：部隊セットコピー
      let _this = this;
      let ret = window.confirm("現在の部隊を [" + _this.sqSquadName[idxTo-1] + "] へ上書きコピーしますが宜しいですか？");
      if (ret) {
        let tag = idxTo;
        for (let i = 1; i <= 6; ++i) {
          let buf = "";
          let squad = document.getElementById("sfSquad"+i);
          squad.childNodes.forEach(function(item){
            buf = buf + item.lang + ",";
          });
          localStorage.setItem('sfSquad'+i+"_"+tag, buf);
        };

        const summamry = document.getElementById("sfSquadSetSummary" + summaryNo);
        summamry.parentElement.open = "";

        _this.mLoadQf(idxTo);
        
        alert("[" + _this.sqSquadName[idxTo-1] + "] へコピーしました。");
      };
    },
  },

  computed: {
    packs_sort() { // 課金パック一覧の絞り込みとソート
      var _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく

      // ソート
      if(this.sort_key1 != "") {
        let set = 1;
        this.sort_asc ? (set = 1) : (set = -1)
        this.packs.sort((a, b) => {
          //if (a[this.sort_key2] > b[this.sort_key2]) return -1;
          //if (a[this.sort_key2] < b[this.sort_key2]) return 1;
          if (a[this.sort_key1] < b[this.sort_key1]) return -1 * set;
          if (a[this.sort_key1] > b[this.sort_key1]) return 1 * set;
          return 0;
        })
      }

      // 絞り込み
      var data = _this.packs;

      //表示対象区分
      let flgDivAll = true;
      _this.divisions.forEach(function(division){
        if(division.check){
          flgDivAll = false;
        }
      })
      if(!flgDivAll) {
        _this.divisions.forEach(function(division){
          if(!division.check){
            data = data.filter(function (row) {
              return row['division'].indexOf(division.name) == -1;
            });
          }
        })
      }

      //入力した条件で絞り込み
      let flgConAll = true;
      _this.contents.forEach(function(content){
        if(content.check){
          flgConAll = false;
        }
      })
      if(!flgConAll) {
        data = data.filter(function (row) {
          let flgFing = false;
          _this.contents.forEach(function(content){
            if(content.check){
              if(row['content'].indexOf(content.name) >= 0) {
                flgFing = true;
              };
            };
          });
          return flgFing;
        });
      }

      //ハイライト設定
      let reg1 = new RegExp("<mark style='background-color: #f7f463;'>",'g');
      let reg2 = new RegExp("<mark style='background-color: #f18960;'>",'g');
      let reg3 = new RegExp("<mark style='background-color: #86ff40;'>",'g');
      let reg4 = new RegExp("<mark style='background-color: #659bff;'>",'g');
      let reg5 = new RegExp("<mark style='background-color: #f742c9;'>",'g');
      let regE = new RegExp("</mark>",'g');
      let num = 1;
      _this.packs.forEach(function(pack){
        pack.content = pack.content.replace(reg1, "");
        pack.content = pack.content.replace(reg2, "");
        pack.content = pack.content.replace(reg3, "");
        pack.content = pack.content.replace(reg4, "");
        pack.content = pack.content.replace(reg5, "");
        pack.content = pack.content.replace(regE, "");
      })
      _this.contents.forEach(function(content){
        if (content.check) {
          let reg3 = new RegExp(content.name,'g');
          _this.packs.forEach(function(pack){
            
            switch(num){
              case 1:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f7f463;'>" + content.name + "</mark>");
                break;
              case 2:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f18960;'>" + content.name + "</mark>");
                break;
              case 3:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #86ff40;'>" + content.name + "</mark>");
                break;
              case 4:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #659bff;'>" + content.name + "</mark>");
                break;
              default:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f742c9;'>" + content.name + "</mark>");
            }
          })
          if(++num > 5){
            num = 1;
          }
        }
      })

      return data
    },

    talks_sort() { // 面談一覧の絞り込みとソート
      let _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく
      let data = _this.talks;
      let keywrod = [1];
      let forceCheck = false;
      
      keywrod[0] = _this.tsSearchWord1.trim();
      //keywrod[1] = _this.tsSearchWord2.trim();
      keywrod[1] = _this.tsSearchWord1s.trim();
      //keywrod[3] = _this.tsSearchWord2s.trim();
      
      // 検索ワードにぁぃぅぇぉ等を入力された場合は平仮名とのマッチングができるよう置換
      let keywrodHira = keywrod[0];
      keywrodHira = keywrodHira.replace(/ぁ/g, 'あ');
      keywrodHira = keywrodHira.replace(/ぃ/g, 'い');
      keywrodHira = keywrodHira.replace(/ぅ/g, 'う');
      keywrodHira = keywrodHira.replace(/ぇ/g, 'え');
      keywrodHira = keywrodHira.replace(/ぉ/g, 'お');
      keywrodHira = keywrodHira.replace(/ゃ/g, 'や');
      keywrodHira = keywrodHira.replace(/ゅ/g, 'ゆ');
      keywrodHira = keywrodHira.replace(/ょ/g, 'よ');
      keywrodHira = keywrodHira.replace(/ゎ/g, 'わ');

      for(let i=0; i<=1; ++i) {
        keywrod[i] = keywrod[i].replace(/!/g, '！');
        keywrod[i] = keywrod[i].replace(/\?/g, '？');
        keywrod[i] = keywrod[i].replace(/１/g, '1');
        keywrod[i] = keywrod[i].replace(/２/g, '2');
        keywrod[i] = keywrod[i].replace(/３/g, '3');
        keywrod[i] = keywrod[i].replace(/４/g, '4');
        keywrod[i] = keywrod[i].replace(/５/g, '5');
        keywrod[i] = keywrod[i].replace(/６/g, '6');
        keywrod[i] = keywrod[i].replace(/７/g, '7');
        keywrod[i] = keywrod[i].replace(/８/g, '8');
        keywrod[i] = keywrod[i].replace(/９/g, '9');
        keywrod[i] = keywrod[i].replace(/０/g, '0');
      };

      // チェックしたニケが誰もいない場合は全員チェックされている扱いとする
      if (!_this.tsTalkNikkes.some(item => item.check === true)) forceCheck = true;

      // 画像認識Onの場合はあいまい検索を行う
      let hitMax = 0;
      let hit;
      if(_this.tsImageManu == 1 || _this.tsImageAuto == 1) {
        let cmp = [1];
        cmp[0] = _this.trigramify(keywrod[0]);
        cmp[1] = _this.trigramify(keywrod[1]);
        //cmp[2] = _this.trigramify(keywrod[2]);
        //cmp[3] = _this.trigramify(keywrod[3]);
        data.forEach(function(nikke) {
          _this.tsTalkNikkes.forEach(function(target){
            if((target.name == nikke.name && target.check) || forceCheck) { // 面談候補でチェックされているニケである、または面談候補が全てチェックされておらず全員対象扱いになっている
              nikke.hit = 0;
              //nikke.cases[1].hit = 0;

              hit = _this.relevance(nikke.trigramify, cmp[0]); // 上選択肢を比較
              if(hit > nikke.hit) {
                nikke.hit = hit;
              };
              if(nikke.hit > hitMax) {
                hitMax = nikke.hit;
              };
              
              hit = _this.relevance(nikke.trigramify, cmp[1]); // 上選択肢を比較
              if(hit > nikke.hit) {
                nikke.hit = hit;
              };
              if(nikke.hit > hitMax) {
                hitMax = nikke.hit;
              };
              
              /*hit = _this.relevance(nikke.cases[1].trigramify, cmp[1]); // 下選択肢を比較(補正後画像)
              if(hit > nikke.cases[1].hit) {
                nikke.cases[1].hit = hit;
              };;
              if(nikke.cases[1].hit > hitMax) {
                hitMax = nikke.cases[1].hit;
              };

              hit = _this.relevance(nikke.cases[1].trigramify, cmp[3]); // 下選択肢を比較(補正後画像)
              if(hit > nikke.cases[1].hit) {
                nikke.cases[1].hit = hit;
              };
              if(nikke.cases[1].hit > hitMax) {
                hitMax = nikke.cases[1].hit;
              };*/
            };
          });
        });
      };

      if(hitMax < 0.2 && _this.tsSearchWordCls == 1) {
        // 画像認識の場合でヒット率が低すぎる場合は更新しない
        data = data.filter(function (nikke) {
          return nikke.disp; // 前回保存していた表示・非表示の値を設定しなおす
        });
      } else {
        data = data.filter(function (nikke) {
          let flgFing = false;
          nikke.disp = false;
          if(keywrod[0] != "") {
            _this.tsTalkNikkes.forEach(function(target){
              if((target.name == nikke.name && target.check) || forceCheck) { // 面談候補でチェックされているニケである、または面談候補が全てチェックされておらず全員対象扱いになっている
                //nikke.cases.forEach(function(caseItem){ //選択肢1～2
                  //if(_this.tsSearchType == "部分一致") {
                    if(keywrod[0] == "*") {
                      nikke.disp = true;
                      flgFing = true;
                    } else if(nikke.select.indexOf(keywrod[0]) >= 0) {
                      nikke.disp = true;
                      flgFing = true;
                    } else if(_this.tsHiragana && nikke.hiragana.indexOf(keywrodHira) >= 0) {
                      nikke.disp = true;
                      flgFing = true;
                    } else if(_this.tsImageManu == 1 || _this.tsImageAuto == 1) {
                      if(hitMax >= 0.2 && nikke.hit >= (hitMax - 0.1)) {
                        nikke.disp = true;
                        flgFing = true;
                        _this.tsCaptureSkipSec = 3;
                      };
                    };
                  //} else {
                  //  if(caseItem.select.startsWith(keywrod[0])) {
                  //    nikke.disp = true;
                  //    flgFing = true;
                  //  } else if(_this.tsHiragana && caseItem.hiragana.startsWith(keywrodHira)) {
                  //    nikke.disp = true;
                  //    flgFing = true;
                  //  } else if(_this.tsImageManu == 1 || _this.tsImageAuto) {
                  //    if(hitMax >= 0.3 && caseItem.hit >= (hitMax - 0.1)) {
                  //      nikke.disp = true;
                  //      flgFing = true;
                  //      _this.tsCaptureSkipSec = 3;
                  //    };
                  //  };
                  //};
                //});            
              };
            });
          };
          return flgFing;
        });
      };

      return data
    }
  },

  destroyed: function () { // PT作成支援関連
    window.removeEventListener('dragstart', function(e){}, false)
    window.removeEventListener('drag', function(e){}, false)
    window.removeEventListener('dragover', function(e){}, false)
    window.removeEventListener('drop', function(e){}, false)
    //clearInterval(this.updateMessageTimer)
  }

}
</script>


